import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Emoji from "react-emoji-render"
import {
  CircuitBoard,
  Link,
  Location,
  Mail,
  MarkGithub,
  Organization,
} from "@primer/octicons-react"
import {
  StyledOcticon,
  Heading,
  Avatar,
  Box,
  Link as PrimerLink,
  BorderBox,
  Text,
} from "@primer/components"
import Toggle from "react-toggle"
import useSiteMetadata from "../hooks/siteMetaData"
import useThemeContext from "../hooks/themeContext"
import styled from "styled-components"

const StyledHr = styled.hr`
  ${props => props.color === "dark" && "border-top-color: whitesmoke;"}
`

export default ({ metaData }) => {
  const { layout } = useSiteMetadata()
  const { style, theme, setTheme } = useThemeContext()

  function onThemeChange(e) {
    const newStyle = e.target.checked ? "dark" : "light"
    setTheme(newStyle)
  }

  const {
    github: { viewer: user },
  } = useStaticQuery(
    graphql`
      query {
        github {
          viewer {
            name
            email
            bio
            login
            url
            avatarUrl
            isHireable
            location
            company
            followers {
              totalCount
            }
            isDeveloperProgramMember
            websiteUrl
            organizations(first: 10) {
              nodes {
                avatarUrl
              }
            }
          }
        }
      }
    `
  )
  const StackedMeta = props => (
    <Box {...props} display={[null, null, "inline-block"]} mr={3} />
  )

  const SideMeta = props => (
    <Box {...props} display={"flex"} alignItems={"center"} mb={3} />
  )

  const MetaComponent = layout === "stacked" ? StackedMeta : SideMeta

  const {
    websiteUrl,
    bio,
    login,
    email,
    organizations,
    avatarUrl,
    isHireable,
    name,
    location,
    isDeveloperProgramMember,
    company,
  } = user
  return (
    <>
      <Avatar
        src={avatarUrl}
        alt="user-avatar"
        className="circle"
        mb={3}
        size={150}
        borderRadius={"50%"}
        maxWidth={"150px"}
      />
      <Heading
        color={style === "dark" ? "white" : undefined}
        mb={2}
        lh={"condensed"}
      >
        {name ? name : login}
      </Heading>
      <Box fontSize={4} mb={2}>
        {name && (
          <MetaComponent>
            <StyledOcticon
              color={theme.iconColor}
              icon={MarkGithub}
              size={20}
              verticalAlign="middle"
              mr={2}
              ariaLabel="GitHub"
            />
            <PrimerLink
              href={`https://github.com/${login}`}
              color={style === "dark" ? "white" : undefined}
            >
              {login}
            </PrimerLink>
          </MetaComponent>
        )}
      </Box>
      <StyledHr color={style} />
      {isDeveloperProgramMember && (
        <MetaComponent color={style === "dark" ? "white" : undefined}>
          <StyledOcticon
            color={theme.iconColor}
            icon={CircuitBoard}
            size={20}
            verticalAlign="middle"
            mr={2}
            ariaLabel="Location"
          />
          <BorderBox
            as={"span"}
            fontSize={5}
            border={0}
            color={"white"}
            bg={"green.5"}
            py={1}
            px={2}
            title="Developer Program Member"
            display={"inline-block"}
            borderRadius={2}
          >
            Developer Program Member
          </BorderBox>
        </MetaComponent>
      )}
      <Text
        as={"p"}
        mb={3}
        fontSize={2}
        color={style === "dark" ? "white" : "gray.5"}
      >
        <Emoji text={bio || ""} />
      </Text>
      <Box mb={6} fontSize={4}>
        {company && (
          <MetaComponent color={style === "dark" ? "white" : undefined}>
            <StyledOcticon
              color={theme.iconColor}
              icon={Organization}
              size={20}
              verticalAlign="middle"
              mr={2}
              ariaLabel="Location"
            />
            {company}
          </MetaComponent>
        )}
        {location && (
          <MetaComponent color={style === "dark" ? "white" : undefined}>
            <StyledOcticon
              color={theme.iconColor}
              icon={Location}
              size={20}
              verticalAlign="middle"
              mr={2}
              ariaLabel="Location"
            />
            {location}
          </MetaComponent>
        )}
        {email && (
          <MetaComponent>
            <StyledOcticon
              color={theme.iconColor}
              icon={Mail}
              size={20}
              verticalAlign="middle"
              mr={2}
              ariaLabel="email"
            />
            <PrimerLink
              href={`mailto:${email}`}
              color={style === "dark" ? "white" : undefined}
            >
              {email}
            </PrimerLink>
          </MetaComponent>
        )}
        {websiteUrl && (
          <MetaComponent>
            <StyledOcticon
              color={theme.iconColor}
              icon={Link}
              size={20}
              verticalAlign="middle"
              mr={2}
              ariaLabel="email"
            />
            <PrimerLink
              href={websiteUrl}
              color={style === "dark" ? "white" : undefined}
            >
              {websiteUrl}
            </PrimerLink>
          </MetaComponent>
        )}
        {isHireable && (
          <BorderBox
            display={"inline-block"}
            fontSize={5}
            borderRadius={2}
            bg={"green.5"}
            py={1}
            px={2}
            title="Hire me"
            border={0}
          >
            Available for hire
          </BorderBox>
        )}
        <StyledHr color={style} />
        <Box mb={1}>
          <Heading mb={2} fontSize={4} color={style === "dark" && "white"}>
            Organizations
          </Heading>
          {organizations &&
            organizations.nodes.map(({ avatarUrl }) => (
              <PrimerLink
                className={"avatar-group-item"}
                style={{ cursor: "pointer" }}
                key={avatarUrl}
              >
                <Avatar size={35} mr={2} src={avatarUrl} />
              </PrimerLink>
            ))}
        </Box>
        <Box mt={16}>
          <Toggle
            defaultChecked={style === "dark"}
            onChange={onThemeChange}
            icons={{
              checked: (
                <img
                  alt="dark-theme"
                  src="data:image/png;base64,"
                  width={16}
                  height={16}
                />
              ),
              unchecked: (
                <img
                  alt="light-theme"
                  src="data:image/png;base64,"
                  width={16}
                  height={16}
                />
              ),
            }}
          />
        </Box>
      </Box>
    </>
  )
}
